<template>
	<view class="box">
		<image lazy-load class="slot-img" src="../../static/images/invite-bg.jpg" mode="widthFix"></image>
		<!-- 我的邀请码 -->
		<view class="my-code">
			<view>我的邀请码</view>
			<view class="num" @tap="copy(shareCode)">{{shareCode}}<text class="iconfont iconfuzhi-"></text></view>
		</view>
		<!-- #ifdef MP -->
		<button open-type="share" class="btn-share"></button>
		<!-- #endif -->
		<!-- 邀请收益 -->
		<view class="income-box">
			<view class="income-box-li">
				<view class="num">
					{{userInviteCount}}<text class="font-small">位</text>
				</view>
				<view class="text">已邀请</view>
			</view>
			<view class="income-box-li">
				<view class="num">
					{{totalMoney | formatNum}}<text class="font-small">元</text>
				</view>
				<view class="text">已赚取</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {mapState} from 'vuex'
	export default {
		data() {
			return {
				shareCode: "",
				userInviteCount:0,
				totalMoney:0
			}
		},
		computed:{
			...mapState({
				user:state=>state.user
			})
		},
		onLoad(option) {
			this.shareCode=this.user.shareCode;
			this.getUserInviteCount()
		},
		// #ifdef MP
		onShareAppMessage(res) {
			return {
				title: '邀您一起体验！',
				path: '/pages/people/index?invite_code='+this.shareCode
			}
		},
		// #endif
		methods: {
			// 获取邀请收益
			getUserInviteCount(){
				this.$api.get('/merchant/getUserInviteCount').then(res=>{
					if(res.code==200){
						let data=res.data;
						this.totalMoney=data.totalMoney
						this.userInviteCount=data.userInviteCount
					}
				})
			},
			// 复制
			copy(data){
				// #ifndef H5
				uni.setClipboardData({
				    data: data,
				    success: function () {
						// app跟小程序会自动弹出提示
				    }
				});
				// #endif
			}
		}
	}
</script>

<style lang="scss">
	.box {
		width: 100%;
		min-height: 100vh;
		height: auto;
		position: relative;
		.my-code {
			position: absolute;
			top: 830rpx;
			width: 100%;
			text-align: center;
			font-size: 26rpx;
			.num {
				font-size:30rpx;
				font-weight:800;
				color: #F35A41;
				margin-top: 10rpx;
				line-height: 40rpx;
				.iconfuzhi- {
					font-size: 24rpx;
					margin-left: 10rpx;
					line-height: 40rpx;
				}
			}
			
		}
		.btn-share{
			position: absolute;
			top: 1180rpx;
			width: 388rpx;
			height: 76rpx;
			z-index: 1;
			left: 50%;
			margin-left:-194rpx ;
			background: transprant;
			opacity: 0;
		}
		.btn-share::after {
			border: none;
		
		}
		.income-box {
			position: absolute;
			top: 1350rpx;
			width: 690rpx;
			height: 250rpx;
			display: flex;
			align-items: center;
			left: 50%;
			margin-left: -345rpx;
			.income-box-li {
				width: 50%;
				text-align: center;
				.num {
					font-size: 56rpx;
					color: #FD6147;
				}
				.font-small {
					font-size: 30rpx !important;
					margin-left: 4rpx;
				}
				.text {
					font-size:24rpx;
					color: #701000;
					margin-top: 16rpx;
				}
			}
		}
	}

	.slot-img {
		width: 100%;
		height: auto;
	}
</style>
